import { Component, h, Host, Prop, State, Watch } from '@stencil/core';
import type { IconName } from '@fortawesome/fontawesome-svg-core';
import { icon, library } from '@fortawesome/fontawesome-svg-core';
import { fal } from '@awesome.me/kit-a1ac577d64/icons';

@Component({
	tag: 'o-icon',
	styleUrl: 'o-icon.scss',
	shadow: true
})
export class OIcon {
	/** The size of the icon, can be 'sm', 'md', 'lg'. */
	@Prop({ reflect: true, mutable: true }) size: 'sm' | 'md' | 'lg' = 'sm';

	/** Optional color for the icon, can be a hex value or a color name. */
	@Prop({ reflect: true }) color?: string = 'oec_blue';

	/** The name of the icon to be rendered. */
	@Prop({ reflect: true }) name: IconName;

	/** Holds the SVG representation of the icon. */
	@State() svg: any;

	/** Holds the size class for the icon based on the size prop. */
	@State() sizeClass: string;

	/** Lifecycle method that runs before the component loads. Initializes the icon library and sets the icon SVG. */
	componentWillLoad() {
		if (fal) library.add(fal);
		this.updateIcon();
		this.setSize();
	}

	/** Watch for changes to the size prop and update the size class accordingly. */
	@Watch('size')
	watchSizeHandler() {
		if (typeof this.size === 'undefined') this.size = 'sm';
		this.setSize();
	}

	/** Updates the SVG based on the current name. */
	private updateIcon() {
		const prefix = 'fal';
		this.svg = icon({ prefix, iconName: this.name })?.html[0] || ''; // Get the SVG for the specified icon
	}

	/** Sets the size class based on the size prop. */
	setSize() {
		switch (this.size) {
			case 'sm':
				this.sizeClass = 'w-4 h-4';
				break;
			case 'md':
				this.sizeClass = 'w-6 h-6';
				break;
			case 'lg':
				this.sizeClass = 'w-8 h-8';
				break;
			default:
				this.sizeClass = 'w-4 h-4'; // Default size 'sm'
		}
	}

	render() {
		const textColorClass = `text-${this.color}`;

		return (
			<Host>
				<div
					class={`antialiased justify-center flex ${textColorClass} items-center ${this.sizeClass}`}
					style={{ color: this.color }}
					innerHTML={this.svg}
				/>
			</Host>
		);
	}
}
